import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class BundleCatalogService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.baseURL;

  addBundle(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/bundle`, payload, {
      headers,
    });
  }

  getBundleByShop(shopId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/bundle/${shopId}?pageSize=10&pageNum=1`,
      {
        headers,
      }
    );
  }

  updateBundle(payload, bundleId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}inventory/bundle/${bundleId}`, payload, {
      headers,
    });
  }

  addItemToBundle(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/bundle/item`, payload, {
      headers,
    });
  }

  addBundleTax(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/bundle/tax`, payload, {
      headers,
    });
  }
  getBundleTaxByShopId(pageSize, pageNum, shopId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/bundle/tax/${shopId}?pageSize=${pageSize}&pageNum=${pageNum}`,
      {
        headers,
      }
    );
  }

  updateBundleTax(payload, bundleId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}inventory/bundle/tax/${bundleId}`, payload, {
      headers,
    });
  }
}
